.header-content {
  padding: 5rem 0 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: left;
}
.header-title-content {
    padding: 0 5%;
    color: var(--color-black);
}
.header-title-content > h1 {
  font-size: 65px;
  color: var(--color-primary);
}

.header-image {
    text-align: center;
}

@media screen and (max-width: 1024px) {
  .header-content {
    padding: 5rem 0 2rem 0;
    position: relative;

  }
  .header-image {
    position: absolute;
    z-index: -1;
    max-width: fit-content;
    max-height: 120vh;
  }
  .header-title-content h1 {
    font-size: 50px;
    padding: 5% 60% 0 0;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: left;
  }
}

@media screen and (max-width: 600px) {
  .header-image {
    position: absolute;
    z-index: -1;
  }
  .header-title-content h1 {
    font-size: 35px;
    padding: 20% 50% 0 0;
  }
}